@import url(https://fonts.googleapis.com/css?family=PT+Sans);

body {
  font-family: 'PT Sans', sans-serif;
  margin: 0;
  padding: 0;
}

html{overflow-x: hidden}
.header {
    background: #43414C;
    color: #fff;
    padding: 10px;
    width: 100%;
}
.pointer {
    cursor: pointer;
}
.tabs {
    cursor: pointer;
    background: #FFF0E9;
    text-transform: uppercase;
}
.icons {
    width: 80px;
    height: auto;
}
.items {
    background-color: #f4f4f4;
    padding: 5px;
    border-bottom: 1px #ccc dotted;
}
.padding {
    padding-left: 50px;
}
.btn-add {
    margin-left: auto;
    margin-right: 35px;
    margin-top: 16px;
    border-radius: 50%;
    border: none;
    padding: 10px;
    cursor: pointer;
} 

@media (max-width: 700px){
    .icons {
        width: 60px;
    }
}
.input {
    margin-top: 55px;
    margin-left: 20px;
    margin-right: 25px;
    width: 40%;
    border: none;
    border-bottom: 2px solid #8c8c8c;
}
.bg {
    width: 40%;
    background: #E60023;
    border-style: none;
    font-size: 20px;
}
.btn-none {
    border: none;
    width: 16px;
    height: auto;
}
.add {
    padding-right: 5px;
}
.minus {
    padding-left: 6px;
}
.order {
    margin-top: 50px;
}
