.tabs {
    cursor: pointer;
    background: #FFF0E9;
    text-transform: uppercase;
}
.icons {
    width: 80px;
    height: auto;
}
.items {
    background-color: #f4f4f4;
    padding: 5px;
    border-bottom: 1px #ccc dotted;
}
.padding {
    padding-left: 50px;
}
.btn-add {
    margin-left: auto;
    margin-right: 35px;
    margin-top: 16px;
    border-radius: 50%;
    border: none;
    padding: 10px;
    cursor: pointer;
} 

@media (max-width: 700px){
    .icons {
        width: 60px;
    }
}